import React, { useState } from 'react';
import { X, AlertCircle, ExternalLink } from 'lucide-react';
import { Button } from './button';

interface MetaMaskModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConnect: () => Promise<void>;
  isLoading: boolean;
  error: string | null;
}

export const MetaMaskModal: React.FC<MetaMaskModalProps> = ({
  isOpen,
  onClose,
  onConnect,
  isLoading,
  error,
}) => {
  const [step, setStep] = useState<'install' | 'connect'>('connect');

  if (!isOpen) return null;

  const handleConnect = async () => {
    try {
      await onConnect();
      onClose();
    } catch (err) {
      console.error('Connection failed:', err);
    }
  };

  const checkMetaMaskInstalled = () => {
    if (typeof window.ethereum !== 'undefined' && window.ethereum.isMetaMask) {
      setStep('connect');
    } else {
      setStep('install');
    }
  };

  React.useEffect(() => {
    checkMetaMaskInstalled();
  }, [isOpen]);

  return (
    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4">
      <div className="glass p-8 rounded-2xl border border-white/20 max-w-md w-full mx-4">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold font-title">Connect Wallet</h2>
          <button
            onClick={onClose}
            className="p-2 hover:bg-white/10 rounded-lg transition-colors"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {step === 'install' ? (
          <div className="text-center space-y-6">
            <div className="w-20 h-20 mx-auto rounded-full bg-orange-500/20 flex items-center justify-center">
              <ExternalLink className="w-10 h-10 text-orange-500" />
            </div>
            <div>
              <h3 className="text-xl font-semibold mb-2">MetaMask Not Found</h3>
              <p className="text-white/70 mb-4">
                Please install MetaMask browser extension to continue.
              </p>
            </div>
            <div className="space-y-3">
              <Button
                onClick={() => window.open('https://metamask.io/download/', '_blank')}
                className="w-full glass-hover border border-white/20"
              >
                Install MetaMask
              </Button>
              <Button
                onClick={onClose}
                variant="outline"
                className="w-full glass-hover border border-white/20"
              >
                Cancel
              </Button>
            </div>
          </div>
        ) : (
          <div className="text-center space-y-6">
            <div className="w-20 h-20 mx-auto rounded-full bg-blue-500/20 flex items-center justify-center">
              <ExternalLink className="w-10 h-10 text-blue-500" />
            </div>
            <div>
              <h3 className="text-xl font-semibold mb-2">Connect to MetaMask</h3>
              <p className="text-white/70 mb-4">
                Click the button below to connect your MetaMask wallet.
              </p>
            </div>

            {error && (
              <div className="p-3 rounded-lg bg-red-500/20 border border-red-500/30 flex items-center gap-2">
                <AlertCircle className="w-4 h-4 text-red-400" />
                <p className="text-sm text-red-400">{error}</p>
              </div>
            )}

            <div className="space-y-3">
              <Button
                onClick={handleConnect}
                disabled={isLoading}
                className="w-full glass-hover border border-white/20"
              >
                {isLoading ? 'Connecting...' : 'Connect MetaMask'}
              </Button>
              <Button
                onClick={onClose}
                variant="outline"
                className="w-full glass-hover border border-white/20"
              >
                Cancel
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
